<style lang="scss">
</style>
<template>
  <Row>
    <Col :span="24">
      <Card style="margin-bottom:20px">当前系统用户人数 {{userNumber}}</Card>
    </Col>
    <Col :span="12">
      <ve-pie :title="{text:'男女比例'}" ref="chart1" v-bind="pie"></ve-pie>
    </Col>
    <Col :span="12">
      <ve-map v-bind="map" ref="chart2" :title="{text:'用户分布'}" height="600px"></ve-map>
    </Col>
  </Row>
</template>
<script>
import { getUserInformation } from "@admin/api/admin";
import { setTimeout } from 'timers';
export default {
  data: function() {
    return {
      userNumber:0,
      chartRefs: ['chart1','chart2'],
      pie: {
        data: {
          columns: ["name", "number"],
          rows: []
        },
        setting: {
          labelMap: {
            number: "比例"
          }
        }
      },
      map: {
        data: {
          columns: ["name", 'number'],
          rows: []
        },
        settings: {
          labelMap: {
            number: "用户人数"
          },
          selectData: true,
          position: "china",
          dimension: "name",
          metrics: ['number']
        }
      }
    };
  },
  created() {
    getUserInformation().then(data => {
      console.log(data);
      this.map.data.rows = data.userAddress;
      this.pie.data.rows=data.userGender;
      this.userNumber=data.userNumber
      this.$nextTick(_ => {
        this.chartRefs.forEach(ref => {
          this.$refs[ref].echarts.resize()
        });
      });
    });
  },
  updated(){
    
  },
  methods: {
  }
};
</script>