import { Account_login,loginout } from '@admin/api/sys.loginandregister'
import {Message} from 'iview'
import router from '../../router';
/*定义数据*/
const state = {
  user: {},
  islogin: false,
}
//定义方法
const mutations = {
  update_user: (state, user) => {
    state.user = user;
  },
  set_is_login: (state, status) => {
    state.islogin = status;
  },
  remove_user: (state) => {
    state.user = {};
    state.islogin = false;
  },
}
const getters = {
  get_user: (state) => {
    return state.user;
  },
  get_loginstatus:state=>state.islogin
}
const actions = {
  login_user: async ({commit,dispatch}, data) => {
    return await Account_login(data).then(resdata => {
      commit("update_user",resdata);
      commit("set_is_login", true);
      return Promise.resolve();
    }).catch(error => {
      return Promise.reject();
    })
  },
  logout:async (store,data)=>{
    return await loginout().then(success=>{
      store.commit("update_user",{});
      store.commit("set_is_login", false);
      Message.success({
        content:"退出成功",
        onClose:()=>{
          router.push("/")
        }
      })
      return Promise.reject();
    }).catch(error => {
      return Promise.reject();
    })
  }
}
const store = {
  state,
  getters,
  actions,
  mutations
}
export default store;