import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user';
Vue.use(Vuex)
import vuexLocalstorage from 'vuex-localstorage';
export default new Vuex.Store({
  modules: {
    user,
  },
  plugins:[
    vuexLocalstorage({
      namespace:'mychat-admin-store',
      initalState:{},
      expires:60*60*1000,//以毫秒为单位
    })
  ]
})
