<template>
  <div>
    <Row class="login">
      <Col class="login-col" :xs="{span:24}" :md="{span:12,push:6}" :lg="{span:7,push:8}">
        <Form ref="loginForm" class="form_box" :model="user" :rules="rules" status-icon>
          <FormItem><h1>管理端</h1></FormItem>
          <FormItem prop="id">
            <Input
              type="text"
              v-model.trim="user.username"
              placeholder="请输入帐号"
              suffix="ios-contact"
              size="large"
            ></Input>
          </FormItem>
          <FormItem prop="pass">
            <InputPassword
              type="password"
              v-model.trim="user.password"
              placeholder="请输入密码"
              size="large"
            ></InputPassword>
          </FormItem>
          <FormItem prop="code">
            <Input type="text" v-model.trim="user.code" placeholder="请输入验证码" size="large">
              <template slot="append">
                <Tooltip effect="dark" content="看不清，换一张" placement="right-end">
                  <div
                    class="codeimg"
                    :style="{'backgroundImage':`url(${imgsrc})`}"
                    @click="clickIMG"
                  ></div>
                </Tooltip>
              </template>
            </Input>
          </FormItem>
          <FormItem>
            <Button
              type="primary"
              class="btn-block"
              @click="login"
              :disabled="disabled"
              :loading="loading"
              long
            >
              <span v-show="!loading&&!disabled">登录</span>
              <span v-show="loading">登录中</span>
              <span v-show="disabled">登录成功</span>
            </Button>
          </FormItem>
        </Form>
      </Col>
    </Row>
  </div>
</template>

<script>
import { loadImgCode } from "@admin/api/sys.code";
export default {
  components: {
    InputPassword: () => import("@/components/inputPassword.vue")
  },
  data: function() {
    return {
      user: {
        username: "",
        password: "",
        code: ""
      },
      rules: {
        username: [
          {
            required: true,
            message: "请输入登录帐号",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "请输入密码",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "请输入验证码",
            trigger: "blur"
          }
        ]
      },
      imgsrc: "",
      loading: false,
      disabled: false
    };
  },
  created() {
    this.clickIMG();
  },
  methods: {
    //换验证码图片
    clickIMG() {
      console.log(1);
      loadImgCode()
        .then(data => {
          console.log(data);
          this.imgsrc = data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    login() {
      let vm = this;
      vm.loading = true;
      let user = vm.user;
      vm.$refs["loginForm"].validate(bool => {
        if (bool) {
          vm.$store
            .dispatch("login_user", user)
            .then(data => {
              vm.$Message.success({
                content: "登录成功",
                onClose: () => {
                  this.$router.push({ path: "/main" });
                }
              });
              vm.disabled = true;
            })
            .catch(error => {
              console.log(error);
              vm.clickIMG();
              vm.user.password = vm.user.code = "";
            })
            .finally(() => (vm.loading = false));
        } else {
          vm.loading = false;
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
.login {
  .login-col{
    max-width: 400px;
    position: absolute;
    // top:50%;
    left:50%;
    transform: translate(-50%,50%);
  }
  .form_box {
    
    padding: 0.5rem;
    box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.95);
    border-radius: 0.2rem;
    // border: 1px solid rgba(0, 0, 0, 0.144);
    // box-shadow: 5px 5px 5px rgba(150, 176, 223, 0.5);
    .form-group {
      height: 55px;
      input,
      button {
        height: 50px;
        border-radius: 0;
      }
      .login-btn {
        min-width: 150px;
        margin: auto;
        width: 100%;
      }
    }
    .codeimg {
      cursor: pointer;
      background-size: 150px 36px;
      width: 150px;
      height: 36px;
    }
  }
}
</style>
<style lang="scss">
.login {
  .ivu-input-group-append {
    padding: 0;
    height: 30px;
    border:none;
  }
}
</style>

