import Vue from 'vue'
import Router from 'vue-router'
import login from '@admin/views/loginadmin'
import main from '@admin/views/main'
import mainindex from '@admin/views/mainindex'
import userManager from '@admin/views/userManager'
Vue.use(Router)

export default new Router({
  // mode: "hash",
  // base: process.env.BASE_URL,
  routes: [{
    path: "/",
    component: login
  },
  {
    path: "/main",
    component: main,
    children:[{
      path:"",
      component:mainindex
    },{
      path:"/userManager",
      component:userManager
    }]
  },
  ]
})
