<template>
  <div class="layout">
    <Layout>
      <Sider
        ref="side1"
        hide-trigger
        collapsible
        :collapsed-width="78"
        v-model="isCollapsed"
        style="height:100vh"
      >
        <Menu :active-name="activename" theme="dark" width="auto" :class="menuitemClasses">
          <MenuItem :name="1" to="/main">
            <Icon type="md-map"></Icon>
            <span>信息统计</span>
          </MenuItem>
          <MenuItem :name="2" to="/userManager">
            <Icon type="md-person" />
            <span>用户管理</span>
          </MenuItem>
        </Menu>
      </Sider>
      <Layout>
        <Header :style="{padding: 0}" class="layout-header-bar">
          <Icon
            @click.native="collapsedSider"
            :class="rotateIcon"
            :style="{margin: '0 20px'}"
            type="md-menu"
            size="24"
          ></Icon>
          <div class="float-rt admin-info">
            <span>欢迎您，管理员{{$store.state.user.user.nickname}}</span>
            <Icon @click.navtive="logout" :style="{margin: '0 20px','cursor':'pointer','color':'#f00'}" custom="mychat-icon-loginout" size="24"></Icon>
          </div>
        </Header>
        <Content :style="{margin: '20px'}">
          <transition mode="out-in" name="el-fade-in-linear">
            <router-view/>
          </transition>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
  data() {
    return {
      isCollapsed: false,
      activename:"1",
      activeto:['/main','/userManager']
    };
  },
  computed: {
    rotateIcon() {
      return ["menu-icon", this.isCollapsed ? "rotate-icon" : ""];
    },
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    ...mapActions(['logout']),
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    updateActiveName(){
      console.log(this.activeto.indexOf(this.$route.path)+1);
      this.activename=this.activeto.indexOf(this.$route.path)+1
    }
  },
  created(){
    this.updateActiveName()
  },
  watch:{
    $route(news){
       this.updateActiveName()
    }
  }
};
</script>
<style scoped lang="scss">
.layout {
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  .layout-header-bar {
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    .menu-icon {
      transition: all 0.3s;
    }
    .rotate-icon {
      transform: rotate(-90deg);
    }
    .admin-info {
      font-size: 16px;
    }
  }
  .menu-item {
    span {
      display: inline-block;
      overflow: hidden;
      width: 69px;
      text-overflow: ellipsis;
      white-space: nowrap;
      vertical-align: bottom;
      transition: width 0.2s ease 0.2s;
    }
    i {
      transform: translateX(0px);
      transition: font-size 0.2s ease, transform 0.2s ease;
      vertical-align: middle;
      font-size: 16px;
    }
  }
  .collapsed-menu {
    span {
      width: 0px;
      transition: width 0.2s ease;
    }
    i {
      transform: translateX(5px);
      transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
      vertical-align: middle;
      font-size: 22px;
    }
  }
}
</style>