import '@admin/plugins/axios'
// 查询用户总数、地区数量、性别数量
export function getUserInformation(){
    return axios.post("/getUserInformation")
}

//获取全部用户
export function selectAllUser(data){
    return axios.post("/selectAllUser",data)
}

//禁用用户
export function prohibitUser(userId){
    return axios.post("/prohibitUser",{userId})
}

//解禁用户
export function releaseUser(userId){
    return axios.post("/releaseUser",{userId})
}