<template>
  <div>
    <div>
      <Select v-model="select.value" style="width:200px;margin:20px;">
        <Option
          v-for="item in select.data"
          :key="item.value"
          :value="item.value"
          :label="item.title"
        ></Option>
      </Select>
    </div>
    <Table v-bind="table"></Table>
    <Page
      :total="page.total"
      show-total
      show-sizer
      show-elevator
      :current.sync="page.current"
      :page-size="page.pageSize"
      :page-size-opts="page.pageSizeOpts"
      @on-change="pageCurrentChange"
      @on-page-size-change="pageSizeChange"
    ></Page>
  </div>
</template>

<script>
import { selectAllUser, prohibitUser, releaseUser } from "@admin/api/admin";
export default {
  data() {
    return {
      table: {
        loading: false,
        stripe: true,
        columns: [
          {
            title: "用户id",
            key: "userId"
          },
          {
            title: "昵称",
            key: "nickname"
          },
          {
            title: "真实姓名",
            key: "name"
          },
          {
            title: "联系方式",
            key: "tel"
          },
          {
            title: "年龄",
            key: "age"
          },
           {
            title: "性别",
            key: "gender"
          },
          {
            title: "地区",
            key: "address"
          },
          {
            title: "操作",
            key: "action",
            width: 300,
            align: "center",
            render: (h, params) => {
              let { status, userId } = params.row;
              return h("div", [
                h(
                  "Button",
                  {
                    props: {
                      type: "primary",
                      size: "small"
                    },
                    style: {
                      marginRight: "5px"
                    },
                    on: {
                      click: () => {
                        this.show(params.row);
                      }
                    }
                  },
                  "查看详情"
                ),
                status == 0
                  ? h(
                      "Button",
                      {
                        props: {
                          type: "error",
                          size: "small"
                        },
                        on: {
                          click: () => {
                            this.remove(userId);
                          }
                        }
                      },
                      "冻结用户"
                    )
                  : h(
                      "Button",
                      {
                        props: {
                          type: "warning",
                          size: "small"
                        },
                        on: {
                          click: () => {
                            this.add(userId);
                          }
                        }
                      },
                      "解冻用户"
                    )
              ]);
            }
          }
        ],
        data: []
      },
      page: {
        total: 0,
        current: 1,
        pageSize: 10,
        pageSizeOpts: [10, 20]
      },

      select: {
        value: 2,
        data: [
          {
            title: "全部信息",
            value: 2
          },
          {
            title: "正常",
            value: 0
          },
          {
            title: "冻结",
            value: 1
          }
        ]
      }
    };
  },
  created() {
    this.getUsers();
  },
  watch: {
    "select.value"(news) {
      this.getUsers();
    }
  },
  methods: {
    getUsers() {
      this.loading = true;
      selectAllUser({
        current: this.page.current,
        pagesize: this.page.pageSize,
        selectvalue: this.select.value
      }).then(({list,total}) => {
        this.table.data = list;
        this.page.total=total
        this.loading = false;
      });
    },
    show(userinfo) {
      this.$Modal.info({
        title: "用户信息",
        content: `头像：<img width="200" height="200" src="/MYCHAT/${
          userinfo.imgurl
        }"/><br>姓名：${userinfo.name}<br>昵称：${userinfo.nickname}<br>年龄：${
          userinfo.age
        }<br>地区：${userinfo.address}`
      });
    },
    remove(userId) {
      prohibitUser(userId).then(() => {
        this.$Message.success("禁用成功");
        this.getUsers();
      });
    },
    add(userId) {
      releaseUser(userId).then(() => {
        this.$Message.success("解禁成功");
        this.getUsers();
      });
    },
    pageCurrentChange(current) {
      this.getUsers();
    },
    pageSizeChange(pagesize) {
      this.page.pageSize = pagesize;
      this.getUsers();
    }
  }
};
</script>

<style>
</style>
